import React from 'react'
import Header from './Header'
import Footer from './Footer'
import Main from './Main'
import PopupWithForm from './PopupWithForm'
import ImagePopup from './ImagePopup'

function App() {
  const [isProfilePopupOpened, setIsProfilePopupOpened] = React.useState(false);
  const [isAddPlacePopupOpen, setIsAddPlacePopupOpen] = React.useState(false);
  const [isEditAvatarPopupOpen, setIsEditAvatarPopupOpen] = React.useState(false);
  const [selectedCard, setSelectedCard] = React.useState();

  function  handleEditAvatarClick() {
    if  (!isEditAvatarPopupOpen) {
      setIsEditAvatarPopupOpen(true) 
    }
  }
  function handleEditProfileClick() {
    if  (!isProfilePopupOpened) {
      setIsProfilePopupOpened(true) 
    }
    }
  function handleAddPlaceClick() {
    if  (!isAddPlacePopupOpen) {
      setIsAddPlacePopupOpen(true) 
    }
  }

  function handleCardClick(card) {
    setSelectedCard(card)
  }

  function closeAllPopups() {
    setIsProfilePopupOpened(false)
    setIsEditAvatarPopupOpen(false)
    setIsAddPlacePopupOpen(false)
    setSelectedCard()
  }

  function handlePopupClick(event) {
    if (event.target.classList.contains("popup")) {
      closeAllPopups()
    }
  }

  React.useEffect(() => {
    if (isProfilePopupOpened || isEditAvatarPopupOpen || isAddPlacePopupOpen || selectedCard) {
      function handleEsc(event) {
        if (event.key === 'Escape') {
          closeAllPopups()
        }
      }

      document.addEventListener("keydown", handleEsc)
      return () => {
        document.removeEventListener("keydown", handleEsc)
      }
    }
  }, [isProfilePopupOpened, isEditAvatarPopupOpen, isAddPlacePopupOpen, selectedCard])

  return (
    <div className="page">
      <Header />
      <Main onEditAvatar={handleEditAvatarClick} onEditProfile={handleEditProfileClick} onAddPlace={handleAddPlaceClick} onCardClick={handleCardClick}/>
      <Footer />
      <PopupWithForm title="Редактировать профиль" name="profile" isOpened={isProfilePopupOpened} onClose={closeAllPopups} onPopupClick={handlePopupClick}>
        <input type="text" placeholder="Ваше имя" id="input-name" className="popup__input" name="input-name" minLength="2" maxLength="40" required />
        <span className="popup__input-error input-name-error"></span>
        <input type="text" placeholder="Род занятий" id="input-job" className="popup__input" name="input-job" minLength="2" maxLength="200" required />
        <span className="popup__input-error input-job-error"></span>
        <input type="submit" className="popup__submit-button" value="Сохранить" name="submit_button" disabled="" />
      </PopupWithForm>
      <PopupWithForm title="Новое место" name="add-card" isOpened={isAddPlacePopupOpen} onClose={closeAllPopups} onPopupClick={handlePopupClick}>
        <input type="text" placeholder="Название" id="input-card-name" className="popup__input" name="input_card_name" minLength="2" maxLength="30" required />
        <span className="popup__input-error input-card-name-error"></span>
        <input type="url" placeholder="Ссылка на картинку" id="input-link" className="popup__input" name="input_link" required />
        <span className="popup__input-error input-link-error"></span>
        <input type="submit" className="popup__submit-button" value="Создать" name="create_button" />
      </PopupWithForm>
      <PopupWithForm title="Вы уверены?" name="confirm" onClose={closeAllPopups} onPopupClick={handlePopupClick}>
        <input type="submit" className="popup__submit-button" value="Да" name="submit_button" />
      </PopupWithForm>
      <PopupWithForm title="Обновить аватар" name="update-avatar" isOpened={isEditAvatarPopupOpen} onClose={closeAllPopups} onPopupClick={handlePopupClick}>
        <input type="url" placeholder="Ссылка на картинку" id="input-avatar" className="popup__input" name="input-avatar" required />
        <span className="popup__input-error input-avatar-error"></span>
        <input type="submit" className="popup__submit-button" value="Сохранить" name="update_button" />
        </PopupWithForm>
        <ImagePopup card={selectedCard} onClose={closeAllPopups} onPopupClick={handlePopupClick}/>
  </div>
  );
}

export default App;
